.progressReport {
  display: flex;
  align-items: center; }

.progressIcon {
  margin-left: .5rem; }

.success, .link {
  color: #80D8FF; }

.link {
  text-decoration: none; }

.description {
  color: #58585F; }

.failure {
  color: #EF5350; }



