.tradingPairPanel {
  flex-grow: 1;
  flex-direction: row;
  border: none;
  z-index: 1; }
  .tradingPairPanel.pairPickerOpen {
    border-bottom-left-radius: 0; }
  @media (max-width: 768px) {
    .tradingPairPanel {
      flex-direction: column;
      border: 1px solid #2B2B33; } }

.priceChartPanel,
.allTradesPanel {
  width: 454px;
  height: 342px; }
  @media (max-width: 992px) {
    .priceChartPanel,
    .allTradesPanel {
      width: 100%; } }

.offerMakePanel {
  height: 474px;
  width: 454px;
  margin-right: 24px;
  flex-grow: 1; }
  @media (max-width: 992px) {
    .offerMakePanel {
      margin-right: 0;
      width: 100%;
      height: 100%; } }

.orderbookPanel {
  height: 474px;
  flex-basis: 454px;
  flex-grow: 1; }
  @media (max-width: 992px) {
    .orderbookPanel {
      width: 100%;
      height: 342px; } }

.myOrdersPanel {
  min-height: 284px;
  width: 100%; }



