.asset {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid #58585F;
  border-radius: .325rem;
  width: 100%;
  height: 100px;
  cursor: pointer;
  color: white;
  background: transparent;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-size: .75rem;
  letter-spacing: 1px; }
  .asset:hover {
    border-color: #707076; }
  .asset.locked {
    pointer-events: none;
    border-color: #3F3F47;
    color: #3F3F47; }
    .asset.locked :global(.icon-background) {
      fill: #3F3F47; }
    .asset.locked :global(.icon-content) {
      fill: #2F2F38; }

.icon {
  height: 38px;
  width: 38px; }



