.settings {
  width: 100%;
  padding: 1rem;
  border: 1px solid #3F3F47;
  border-radius: .25rem; }

.parameter {
  display: flex;
  flex-direction: column;
  width: 50%; }
  .parameter .name {
    display: inline-block;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.9);
    letter-spacing: 1px;
    margin-bottom: .75rem; }
  .parameter .value {
    text-align: left;
    padding: 0 1rem; }

.warning {
  display: flex;
  align-items: center;
  width: 100%;
  padding: .875rem;
  margin-top: 1rem;
  border: 1px solid #3F3F47;
  border-radius: .25rem; }
  .warning .icon {
    height: 24px;
    width: 24px;
    margin-right: 12px; }
    .warning .icon :global(.warning-icon) {
      fill: rgba(255, 255, 255, 0.9); }
  .warning .text {
    font-weight: 500;
    font-size: 12px;
    width: fit-content;
    color: #97979B;
    text-align: left;
    letter-spacing: .7px;
    line-height: 16px;
    margin: 0; }
  .warning .highlight {
    font-weight: 600;
    color: white; }



